body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #FFFFFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Routing Transition CSS */
.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

#root { 
  font-family: 'Roboto', sans-serif;
  height: 100%;
  overflow: hidden;
}

.routing-body {
  min-height:100%;
}
.content-body {
  min-height:100%;
  margin-bottom:50px;
}

.footer-container {
  width: 100%;
  background: #9BD1E5;
  height: 50px;
  z-index: 1;
  font-size: 1em;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  left: 0;
  bottom: 0;
}

.footer-container p {
  color: #353535;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 20px;
  padding: 0px;
}

.layout-drawer {
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  height:100%;
}

.layout-drawer .social-links {
  justify-content: space-evenly;
  margin-bottom: 30px;
}

.layout-drawer .social-links a {
  font-size: 2em;
}

.mdl-layout__header {
  box-shadow: none !important;
}

.top-color-bar {
  width: 100%;
  height: 3px;
  background: #F26419;
}

body {
  overflow-x: auto;
  overflow-y: hidden;
}
/* Main layout styles, to be used across all pages */

.layout-header {
  background: rgba(0,0,0,0) !important;
  border: none;
}

body {
  background: transparent;  /* fallback for old browsers */
  background: #fff5b3;
  /*
  background: -webkit-linear-gradient(to top left, #FFFFFF, #fff5b3);
  background: linear-gradient(to top left, #FFFFFF, #fff5b3);
  */
}

.layout-header, .layout-header a, .layout-drawer a:hover,
.layout-drawer a, .layout-drawer a:hover {
  color: #353535;
}

.mdl-layout__header-row .mdl-navigation__link, .mdl-layout__drawer-button {
  color: #353535 !important;
}

@media only screen and (max-width: 1024px){
  .routing-body{
    margin-top:50px;
  }
}

.layout-drawer a {
  text-decoration: none;
  font-family: "Roboto" sans-serif;
}


.drawer-navigation {
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 80%;
}

.drawer-title {
  font-size: 2em;
  padding: 0;
  margin-bottom: -10px;
  margin-top: 30px;
  flex-grow: 0.5;
}

.nav-color-bar {
  width: 150px;
  height: 2px;
  background: #FCAF58;
  margin: 0 !important;
  padding: 0 !important;
  align-self: center;
  justify-self: flex-start;
}

.nav-color-bar:hover {
  background: #FCAF58 !important;
}

.drawer-links-list {
  display:flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  flex-grow: 3;
  text-align:left;
}
.drawer-links-list a {
 margin-top: 30px;
 width: 120px;
}

.drawer-links-list a i {
  background: #FFFFFF;
  padding:10px;
  border-radius: 50%;
  width: 12px;
  text-align:center;
  margin-right: 15px;
}

.layout-drawer .social-links a {
  color: #3C6E71;
}

.layout-drawer .social-links a:hover {
  color: #9BD1E5;
}


/* Landing Page Styles */


.landing-background {
    background: url(/static/media/flower.f89bcfab.jpg);
    background-size: cover;
    filter: blur(8px) brightness(3);
    -webkit-filter: blur(8px) brightness(3);
    position: fixed;
    top: 0;
    left: 0;
    margin-top: 10px;
    min-width: 100%;
    min-height: 100%;
    overflow:hidden;
    z-index: -1;
}

.landing-body {
    height:100%;
    color: #D3D0CB;
    padding:0px;
    margin:0px;
    border-top: none;
}

.landing-content {
    margin-top:70px;
    z-index: 2;
    margin-bottom: 10px;
    display:flex;
    flex-direction: column;
    background-color: none;
    padding-bottom: 20px;
    margin-left: 20px;
    margin-right: 20px;
}


.landing-body .nextpage-link {
    background: #9BD1E5;
    padding: 10px;
    margin-top:20px;
    margin-left: 90px;
    width: 350px;
    box-shadow: 0px 2px 2px 0px #3C6E71;
}

.landing-body .nextpage-link a {
    font-size: 1.4em;
    font-family: "Roboto" sans-serif;
    font-weight: 700; 
    text-shadow: 0px 0px #000;
    text-decoration: none;
    margin-right:10px;
    border-radius: 2;
    padding:10px;
    color: #3C6E71;
}

.landing-body .nextpage-link:hover {
    background: #9fdef5;
    color: #49888c;
}


.circles-background h1 {
    font-size: 6em;
    font-family: "Roboto" sans-serif;
    color: #FFFFFF;
    text-shadow: 4px 0px #3C6E71;
    white-space: nowrap;
    margin:0;
    margin-top: 10px;
    padding:0;
    margin-left: 80px;

}

.circles-background {
        width: 100%;
        height: 135px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex-wrap: nowrap;
        justify-content: center;
}

.landing-content h4 {
    color: #D3D0CB;
    text-shadow: 1px 0px #3C6E71;
    font-weight: 100; 
    margin-left: 90px;
    margin-top: 0;
    align-self: flex-start;
    text-align: center;
}


  


@media only screen and (max-width: 1024px){
    .circles-background {
        align-items: center;
        justify-content: flex-end;
        background: none;
        height:120px;
    }
    .circles-background h1 {
        font-size: 3.8em;
        margin: 10px;
        padding: 0px;
        margin-bottom: 20px;
        margin-top: 0px;
    }
    .landing-content h4 {
        margin-right: 0px;
        margin-top: -10px;
        font-size: 20px;
        margin-left: 0;
        margin-right: 0;
        align-self: center;
    }
    .landing-content {
        margin-top:120px;
        align-items: center;
        justify-content: center;
    }
    .landing-body .nextpage-link {
        width: 240px;
        text-align: center;
        align-self: center;
        margin-right:0px;
        margin-left:0px;
    }
}
/* Contact Page Styles */

.contact-body {
    min-height: 100%;
    background: linear-gradient(
        to top,
        #fff5b3,
        #fff5b3 50%,
        #fff6d6 50%,
        #fff6d6
        );
    background-size: 100% 20px;
}

.contact-container {
    min-height:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-table {
    max-width: 800px;
    min-width: 400px;
    background: #FFFFFF;
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items:center;
    padding: 10px;
    margin-top: 15px;
    margin-bottom: 30px;
    box-shadow: 0px 2px 2px 0px #7a320c;
}

.contact-text {
    min-width:300px;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.contact-table h1, .contact-table p {
    color: #353535;
}

.contact-table h1 {
    font-size: 36px;
    font-family: "Roboto" sans-serif;
    font-weight: 700;
    margin-bottom: 40px;
    padding: 0;
}


.contact-table p {
    margin: 10px;
    padding-top: 20px;
    padding-bottom:20px;
    max-width: 300px;
}

.contact-table ul {
    list-style-type: none;
}

.contact-table li {
    text-align: left;
}

.social-links {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom:20px;
}

.contact-table .social-links a {
    font-size: 4em;
    color: #3C6E71;
    padding: 20px;
}

.contact-table .social-links a:hover {
    color: #9BD1E5;
}

.contact-table img {
    align-self: center;
    width: 400px;
    min-width: 300px;
    
}

.red-color-bar {
    width: 250px;
    height: 2px;
    background: #FCAF58;
    align-self: center;
}

.red-color-bar.bottom {
    margin-bottom:30px;
}

@media only screen and (max-width: 800px){
    .contact-table {
        display:block;
        margin-left:5px;
        margin-right:5px;
    }
    .contact-table img {
        display:none;
    }
}

@media only screen and (max-width: 1024px){
    .contact-table {
        margin-top: 50px;
        margin-bottom:70px;
        
    }
    .contact-container {
        margin-top: 50px;
    }
}

/* Projects Page Styles */




/* Normal Styles */

.projects-body {
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    
}

.projects-background {
    width: 100%;
    height: 100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    background: linear-gradient(
        to bottom,
        #fff5b3,
        #fff5b3 50%,
        #fff6d6 50%,
        #fff6d6
        );
    background-size: 100% 20px;
}

.card-intro {
    width: 100%;
    align-self: center;
    background:#fff;
}

.card-intro h1 {
    font-size: 36px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    margin: 10px;
    padding: 0;
}


.projects-body, .projects-body h2, .projects-body h3{
    font-family: "Roboto", sans-serif;
    font-weight: 100;
}


.projects-body li, .projects-body ul {
    list-style: outside none none;
    text-decoration: none;
    padding: 0px;
}


.projects-body .cards h3 {
    font-size: 1.2em;
    margin:0px;
}

.projects-body .cards ul {
    font-size: 1em;
    margin:0px;
}

.projects-body .card-title {
    width: 340px;
    /*background: #753335; Card title default color */
    color: #D3D0CB;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 24px;
    text-shadow: 2px 0px #000;
    border-bottom: 1px solid #D3D0CB;
    background-image: linear-gradient(to bottom right, #b55b5b, #753335);
}

.card-title h2, .card-title h3 {
    margin: 0;
    padding: 0;
}

.cards-container {
    display:flex;
    flex-direction: column;
    width:100%;
    padding-bottom: 50px;
    margin-bottom: 30px;
}

.cards {
    margin-left:auto;
    margin-right:auto;
    padding:0;
}

.projects-body .card {
    background-color: #D3D0CB; /*Card text background*/
    margin: 10px;
    box-shadow: 0px 2px 2px 0px #7a320c;
}

.projects-body .card, .card-text, .card-text h4, .card-intro h1 {
    color: #353535;
}

.card-text {
    width: 340px;
    min-height:150px;
    background: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap:nowrap;
    align-items: center;
    margin-right: 30px;
    margin:0;
    padding:0;
    text-align: left;
    color: #2B2226;
}

.card-text-inner {
    margin:40px;
}

.card-text h4 {
    font-size : 18px;
    font-weight: 700;
    margin:0;
    padding:0;
}

.card-title.blue-card {
    background-image: linear-gradient(to bottom right, #b55b5b, #3C6E71);
}
.card-title.green-card {
    background-image: linear-gradient(to bottom right, #8AEA92, #753335);
}
.card-title.orange-card {
    background-image: linear-gradient(to bottom right, #eca468, #753335);
}

.card-intro h5 {
    color: #D3D0CB;
    font-weight: 100;
}

.projects-body .nextpage-container {
    margin-top: 50x;
    margin-bottom:50px;
    padding: 10px;
    width: 300px;
    align-self: center;
    background: #9BD1E5;
    box-shadow: 0px 5px 5px #000;
    border-radius: 2;
}

.nextpage-container a {
    font-weight:700;
    color: #3C6E71;
    text-decoration: none;
    font-size: 1.4em;
}

.nextpage-container:hover {
    background: #9fdef5;
    color: #49888c;
}

.project-type{
    align-self: flex-end;
    padding: 0;
    margin: 0;
    padding-right: 5px;
    padding-left: 5px;
    background-color: #353535;
}

.project-type h5 {
    font-size: 16px;
    margin: 5px;
}

.card-icon {
    background-color: #353535;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    margin-left: 10px;
}
.card-icon i {
    font-size: 24px;
    text-shadow: none;
}


/* Small Screen Styles */
@media only screen and (max-width: 1024px) {

    .projects-body {
        margin-top: 50px;
    }
  }
/* About Me Page Styles */


.about-body {
    color:#353535;
    padding:0px;
    margin:0px; 
    display:flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(
        to bottom,
        #fff6d6,
        #fff6d6 50%,
        #fff5b3 50%,
        #fff5b3
        );
    background-size: 100% 20px;

}


.about-body hr {
    width:100%;
    border-top: solid 1px #353535;
}
.about-table {
    min-width: 500px;
    max-width: 800px;
    margin-bottom: 50px;
    background: #fff;
    padding: 20px;
    margin-top: 15px;
    box-shadow: 0px 2px 2px 0px #7a320c;

}

.about-row, .myworks-row {
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    align-content:center;
    flex-wrap:wrap;
}

.about-body img {
    width: 200px;
    height: 200px;
    background: #9BD1E5;
    padding: 20px;
}

.aboutme-img img {
    background: #9BD1E5;
}

.aboutme-img {
    text-align: right;
    margin-right: 20px;
    box-shadow: 0px 5px 5px 0px #FCAF58;
}
.aboutme-cell {
    text-align: left;
    max-width: 400px;
    padding-right: 20px;
 
}
.myworks-cell {
    text-align: right;
    max-width: 400px;
    padding-left: 20px;
    padding-bottom: 20px;
    margin-top: 20px;
}
.myworks-img {
    text-align: left;
    margin-left: 20px;
    box-shadow: 0px 5px 5px 0px #FCAF58;
    
}

.about-body .nextpage-container {
    color: #fff;
    text-decoration: none;
    margin-top: 30px;
    margin-bottom: 80px;
}

.about-body .nextpage-link {
    background: #9BD1E5;
    color: #3C6E71;
    padding: 10px;
    padding-left: 70px;
    padding-right: 70px;
    font-size: 1.4em;
    text-decoration: none;
    font-family: "Roboto" sans-serif;
    font-weight: 700;
    box-shadow: 0px 5px 5px 0px #FCAF58;
    border-radius: 2;
}

.about-body .nextpage-link:hover {
    background: #9fdef5;
    color: #49888c;
}

.about-body h4 {
    font-size: 20px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    margin: 0;
    padding: 0;
    margin-bottom: -10px;
    margin-top: 10px;
    text-align: center;
}

.color-block-left, .color-block-right {
    width: 150px;
    height: 15px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.color-block-left {
    float:left;
    background: #F26419;
}

.color-block-right {
    float: right;
    background: #F26419;
}


@media only screen and (max-width: 550px) {
    .about-body {
        padding:0px;
        margin:0px;
        display:flex;
        flex-direction: column;
        align-items: center;
    }

    .myworks-img, .aboutme-img {
        margin: 0;
    }

}

@media only screen and (max-width: 500px) {
    .about-table {
        box-shadow: none;
    }
}


@media only screen and (max-width: 1024px) {
    .aboutme-cell, .myworks-cell {
        width:300px;
        padding-left: 0;
        padding-right: 0;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .color-block-left, .color-block-right{
        display:none;
    }
    .aboutme-cell h4 {
        margin-top: 20px;
    }
    .about-body{
        margin-top: 50px;
    }

}
